@font-face {
    font-family: Without;
    src: url(/fonts/WithoutAltSans-Black.ttf);
}

@font-face {
    font-family: Droid;
    src: url(/fonts/DroidSerif.ttf);
}

@font-face {
    font-family:Monserrat ;
    src: url(/fonts/Montserrat-SemiBold.otf);
}

body{
    margin: 0;
    padding: 0;
}

//css for deel
.deel1{
    display: flex;
    flex-direction: row;
    width: 100vw;
    align-items: center;
    justify-content: space-around;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-top: 2vh;
}

.deel1 a{
    text-decoration: none;
    color:#013335 ;
}

.logo{
    width: 100vw;
    display: flex;
    justify-content: center;
}

.logo img{
    width: 3vw;
}


.nav{
    display: flex;
    flex-direction: row;
    width: 100vw;
    justify-content: center;
}

.nav .main{
    font-weight: bolder;
    font-family: Monserrat;
}



.dropdown {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 20vw;
    position: relative; /* Important for positioning dropdown-content */
    transition: all 1s ease-in-out;
  }
  
  .dropdown-content {
    display: flex;
    flex-direction: column;
    margin-top: 3vh;
    visibility: hidden;
    opacity: 0; /* Ensure initial opacity is 0 */
    position: absolute; /* Position relative to dropdown */
    top: 100%; /* Position below dropdown */
    left: 5vw;
    width: 10vw;
    background-color: gold; /* Optional: Set background color */
    transition: all 1s ease-in-out;
  }

  .dropdown-content a{
    padding: 5px;
  }
  
  .dropdown:hover .dropdown-content {
    visibility: visible;
    opacity: 1; /* Show dropdown content on hover */
  }

.phone-links{
    display: flex;
    flex-direction: column;
}

.hamburger-class{
    width: 100vw;
    display: none;
}

@media (max-width : 1000px) {
    .nav{
        display: none;
    }
    .hamburger-class{
        display: block;
    }
}



.swipe-in{
    transform: translateX(100%);
    animation: swipe-in 1s ease-in-out forwards;
}

.swipe-out{
    transform: translateX(0%);
    animation: swipe-out 1s ease-out forwards;
}

@keyframes swipe-in {
    from{
        opacity: 0;
        transform: translateX(100%);
    }

    to{
        opacity: 1;
        transform: translateX(0%);
    }
}

@keyframes swipe-out {
    from{
        opacity: 1;
        transform: translateX(0%);
    }

    to{
        opacity: 0;
        transform: translateX(100%);
    }
}
